import React from 'react'
import { Link } from 'react-router-dom';
import styles from './Home.module.css'
import imgBG from './images/etc/Main.png'
import imgGaeBock from './images/etc/C_12.png'
import imgFonFon from './images/etc/under_white_bold.png'

function Home()
{
    return (
        <>
        <div className={styles.Home}>
            <img className={styles.MainImage} src={imgBG} />
            {/* <img className={styles.GaebokImage} src={imgGaeBock} /> */}
            <Link style={{ textDecoration: 'none'}} to='/main'>
                <button className={styles.StartButton}>테스트 시작하기!</button>
            </Link>
            <img className={styles.FooterImage} src={imgFonFon}/>
        </div>
        </>
    );
}

export default Home;